import React, { useRef, useEffect } from 'react';
import { Map, View, Overlay } from 'ol';
import 'ol/ol.css';
import Tile from 'ol/layer/Tile';
import Vector from 'ol/layer/Vector';
import OSM from 'ol/source/OSM';
import { fromLonLat, toLonLat } from 'ol/proj';
import { Icon, Style } from 'ol/style';
import { Vector as VectorSource } from 'ol/source';
import { Feature } from 'ol';
import { Point } from 'ol/geom';
import { DragPan } from 'ol/interaction';
import pin from './pin-55.svg';

const MapComponent = () => {
    const mapRef = useRef(null);

    useEffect(() => {
        // ฟังก์ชันดึงข้อมูลตำแหน่งจาก URL
        const getLocationsFromUrl = () => {
            const url = new URL(window.location.href);
            const locationsParam = url.searchParams.get('locations'); // รับพารามิเตอร์ 'locations'
            if (!locationsParam) return [];
            
            return locationsParam.split(';').map((loc) => {
                const [lat, lng, ...nameParts] = loc.split(',');
                const name = nameParts.join(','); // กรณีชื่อมี ',' ให้รวมกลับ
                return { 
                    lat: parseFloat(lat), 
                    lng: parseFloat(lng), 
                    name 
                };
            }).filter((loc) => !isNaN(loc.lat) && !isNaN(loc.lng)); // กรองพิกัดที่ไม่ถูกต้อง
        };

        const locations = getLocationsFromUrl();
        if (locations.length === 0) {
            console.warn('No valid locations found in URL. Using default center.');
            locations.push({ lat: 0, lng: 0, name: 'Default Location' });
        }

        // สร้างฟีเจอร์สำหรับตำแหน่ง
        const features = locations.map((loc) => {
            return new Feature({
                geometry: new Point(fromLonLat([loc.lng, loc.lat])),
                name: loc.name,
            });
        });

        // สร้าง Overlay สำหรับแสดงชื่อ
        const overlays = locations.map((loc) => {
            const element = document.createElement('div');
            element.className = 'location-label';
            element.innerText = loc.name;

            const overlay = new Overlay({
                element,
                position: fromLonLat([loc.lng, loc.lat]),
                positioning: 'bottom-center',
                offset: [0, -10],
            });

            return overlay;
        });

        // สร้างแผนที่
        const map = new Map({
            target: mapRef.current,
            layers: [
                new Tile({
                    source: new OSM(),
                }),
                new Vector({
                    source: new VectorSource({
                        features: features,
                    }),
                    style: new Style({
                        image: new Icon({
                            src: pin,
                            scale: 0.15,
                            anchor: [0.5, 1],
                        }),
                    }),
                }),
            ],
            view: new View({
                center: fromLonLat([locations[0].lng, locations[0].lat]),
                zoom: 12,
            }),
        });

        // เพิ่ม Overlays ลงในแผนที่
        overlays.forEach((overlay) => map.addOverlay(overlay));

        const maxLatitude = 85;
        const minLatitude = -85;

        const dragPanInteraction = new DragPan({
            handleDragEvent: function (event) {
                const view = map.getView();
                const center = toLonLat(view.getCenter());
                const lat = center[1];
                if (lat > maxLatitude || lat < minLatitude) {
                    return;
                }
                DragPan.prototype.handleDragEvent.call(this, event);
            },
        });

        map.removeInteraction(map.getInteractions().getArray().find((i) => i instanceof DragPan));
        map.addInteraction(dragPanInteraction);

        return () => {
            map.getInteractions().clear();
            map.setTarget(undefined);
        };
    }, []);

    return (
        <>
            <div ref={mapRef} style={{ width: '100%', height: '100vh' }} />
            <style>
                {`
                .location-label {
                    background-color: white;
                    border: 1px solid black;
                    padding: 2px 5px;
                    font-size: 12px;
                    border-radius: 4px;
                    white-space: nowrap;
                }
                `}
            </style>
        </>
    );
};

export default MapComponent;
